.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2rem 0 0 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__center {
    margin: 0 1.875rem;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 5.25rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    font-family: 'Arsenal';
    font-size: 0.875rem;
    line-height: 180%;
    color: #FFF;
    margin-left: 1.875rem;
    span {
      font-weight: 700;
      color: #FFC700;
    }
  }
  &__subtitle {
    font-family: 'RussoOne';
    font-size: 1rem;
    line-height: 130%;
    color: #ffffff;
    padding-left: 2rem;
  }
  &__link {
    display: flex;
    align-items: center;
    color: #FFC700;
    margin-top: 0.5rem;
    &:hover {
      color: #ffffff;
    }
    i {
      flex-shrink: 0;
      width: 1rem;
      display: flex;
      align-items: center;
      margin-right: 1rem;
    }
    i svg {
      width: 100%;
      height: auto;
    }
    i svg path {
      fill: currentColor;
    }
    span {
      font-family: 'Arsenal';
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 700;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 0.625rem;
  }
  &__social {
    width: 2rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFC700;
    border-radius: 50%;
    &:hover {
      background: #ffffff;
    }
    svg {
      width: 1rem;
      height: auto;
    }
  }
  &__group {
    display: flex;
    flex-direction: column;
    text-align: right;
    margin-left: 1.875rem;
  }
  &__phone {
    display: block;
    span {
      font-family: 'RussoOne';
      font-size: 1rem;
      line-height: 130%;
      color: #FFF;
      transition: all 0.3s;
    }
    &:hover {
      span {
        color: #FFC700;
      }
    }
  }
  .header__btn {
    justify-content: flex-end !important;
    background: transparent;
    padding: 0;
    margin-top: 0.125rem;
    &:hover {
      color: #ffffff;
    }
    span {
      font-family: 'Arsenal';
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 700;
      text-decoration: underline;
      text-underline-offset: 0.5rem;
      text-decoration-style: dashed;
      color: #FFC700;
      overflow: unset;
    }
  }
}

@media screen and (max-width: 1023px) {
  .header {
    padding-top: 1.25rem;
    &__center {
      margin: 0 1.5rem;
    }
    &__logo {
      width: 4.6875rem;
    }
    &__text {
      max-width: 12.5rem;
      margin-left: 1rem;
      br {
        display: none;
      }
    }
    &__group {
      margin-left: 1rem;
    }
    &__phone {
      span {
        font-size: 0.875rem;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__center {
      flex-grow: 1;
    }
    &__text {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__subtitle {
      font-size: 0.875rem;
      padding-left: 1.5rem;
    }
    &__link {
      i {
        margin-right: 0.5rem;
      }
      span {
        font-size: 0.75rem;
      }
    }
    &__social {
      width: 1.5rem;
      height: 1.5rem;
      svg {
        width: 0.75rem;
      }
    }
    &__phone {
      span {
        font-size: 0.875rem;
      }
    }
  }
}

@media screen and (max-width: 499px) {
  .header {
    &__center {
      display: none;
    }
    &__right {
      margin-left: 1rem;
    }
  }
}