html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}
body {
  font-family: 'OpenSans', sans-serif;
}
.fixed-body {
  overflow: hidden;
}
.wrapper {
  position: relative;
  overflow: hidden;
}
.container-fluid {
  max-width: 1630px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'RussoOne';
}
.btn {
  span {
    min-width: auto;
    padding: 0;
  }
}
img {
  object-fit: cover;
}