.section-group {
  background: #000000;
}
.main {
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: left;
  background-size: cover;
  padding: 15.625rem 0 13.75rem;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
  }
  &__left {
    position: relative;
    z-index: 2;
    width: 100%;
    max-width: 48.75em;
    margin-right: 2rem;
  }
  &__right {
    position: relative;
    max-width: 23.125rem;
    padding-top: 80px;
    &:after {
      content: '';
      position: absolute;
      top: -3.125rem;
      right: 1rem;
      width: 45rem;
      height: 45rem;
      background: linear-gradient(180deg, #000 0%, rgba(0, 0, 0, 0.00) 100%);
      backdrop-filter: blur(0.625rem);
      border-radius: 50%;
    }
    &:before {
      content: '';
      position: absolute;
      top: -8.125rem;
      right: -4rem;
      width: 55rem;
      height: 55rem;
      background-image: url("../img/ellipse.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    .main__quote {
      margin-left: 0;
    }
  }
  &__right-inner {
    position: relative;
    z-index: 2;
  }
  h1 {
    max-width: 43.75rem;
    font-size: 3.75rem;
    line-height: 130%;
    letter-spacing: 0.1875rem;
    text-transform: uppercase;
    color: #ffffff;
    margin: 0 0 10.625rem 0;
    span {
      display: inline-block;
      letter-spacing: 0;
      color: #FFC700;
      margin-top: 1rem;
    }
  }
  &__quote {
    max-width: 24.0625rem;
    background: rgba(0, 0, 0, 0.20);
    border: 0.0625rem dashed #FFF;
    backdrop-filter: blur(0.625rem);
    border-radius: 1.25rem;
    padding: 1.5rem;
    margin-left: auto;
    &-text {
      font-family: 'Arsenal';
      font-style: italic;
      font-size: 1.875rem;
      line-height: 130%;
      font-weight: 700;
      text-transform: uppercase;
      color: #FFC700;
    }
    &-text2 {
      font-family: 'Arsenal';
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      color: #FFF;
      strong {
        display: block;
        font-size: 1.5rem;
        color: #FFC700;
        margin-top: 0.625rem;
      }
    }
  }
  &__subtitle {
    position: relative;
    font-family: 'RussoOne';
    font-size: 1.875rem;
    line-height: 130%;
    color: #ffffff;
    padding-left: 1.875rem;
    &:after {
      content: '';
      position: absolute;
      top: 0.4375rem;
      left: 0;
      width: 1.25rem;
      height: 1.25rem;
      background: #FFC700;
      border-radius: 50%;
    }
  }
  &__text {
    max-width: 19.375rem;
    font-size: 1.125rem;
    line-height: 160%;
    color: #FFF;
    margin-top: 0.625rem;
    margin-bottom: 3.125rem;
  }
  &__btn {
    width: 15rem;
    height: 3.75rem;
    margin-top: 2.5rem;
  }
  &__info {
    max-width: 16.875rem;
    font-family: 'RussoOne';
    font-size: 1rem;
    color: #FFF;
    margin-top: 2.5rem;
    span {
      color: #FFC700;
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    top: -6.25rem;
    left: calc(-100% - 4rem);
    width: 40rem;
    max-width: unset;
    max-height: unset;
    pointer-events: none;
  }
  .cat {
    position: absolute;
    top: 11.25rem;
    left: 14rem;
  }
}
.info {
  padding: 0 0 15.625rem 0;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    background: linear-gradient(#A9673B, #00000000);
    padding: 8.75rem 2.5rem 12.5rem 7.5rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.625rem);
      height: calc(100% - 0.625rem);
      background: #000000;
      pointer-events: none;
    }
  }
  &__left {
    position: relative;
    z-index: 2;
    margin-right: 2rem;
  }
  &__right {
    position: relative;
    z-index: 2;
  }
  h2 {
    max-width: 39.375rem;
  }
  &__items {
    position: relative;
    max-width: 31.25rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 3.125rem;
    background: linear-gradient(rgba(255, 199, 0, 0.7), #00000000);
    border-radius: 1.25rem;
    padding: 3.125rem 6.25rem;
    margin-left: auto;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.25rem);
      height: calc(100% - 0.25rem);
      background: #000000;
      border-radius: 1.25rem;
      pointer-events: none;
    }
  }
  &__item {
    position: relative;
    z-index: 1;
    &-title {
      font-family: 'RussoOne';
      font-size: 2.375rem;
      line-height: 130%;
      color: #FFF;
    }
    &-text {
      font-size: 1rem;
      line-height: 130%;
      color: #ffffff;
      span {
        color: #FFC700;
      }
    }
  }
  &__item:nth-child(2) {
    transition: all 0.6s;
  }
  &__item:nth-child(3) {
    transition: all 0.8s;
  }
  &__quote {
    max-width: 37.5rem;
    background: rgba(0, 0, 0, 0.70);
    border: 0.125rem dashed #FFC700;
    border-radius: 1.125rem;
    padding: 1.25rem 1.875rem;
    font-family: 'RussoOne';
    font-size: 1.875rem;
    line-height: 130%;
    color: #FFC700;
    margin-top: 2.5rem;
    margin-left: auto;
  }
  &__img {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 2.5rem;
    width: 66.5rem;
  }
  &__img2 {
    position: absolute;
    z-index: 1;
    left: -11.875rem;
    bottom: -9.375rem;
    width: 21.875rem;
    max-width: unset;
    max-height: unset;
  }
  &__img3 {
    position: absolute;
    z-index: 2;
    right: -11.875rem;
    bottom: -9.375rem;
    width: 20.625rem;
    max-width: unset;
    max-height: unset;
  }
  &__img4 {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: -6rem;
    width: 39.375rem;
  }
  &__img5 {
    position: absolute;
    z-index: 1;
    left: 50%;
    bottom: -15rem;
    transform: translate(-50%, 0);
    width: 61.25rem;
  }
  &__img6 {
    position: absolute;
    z-index: 1;
    right: 0;
    bottom: -16rem;
    width: 51.875rem;
  }
}
.works {
  padding: 0;
  &__inner {
    background: #121212;
    border-radius: 1.25rem;
    padding: 3.75rem 2.5rem;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  h2 {
    max-width: 48.75rem;
  }
  &__quote {
    max-width: 30.3125rem;
    border: 0.125rem dashed #FFC700;
    border-radius: 1.25rem;
    padding: 1.25rem;
    font-family: 'RussoOne';
    font-size: 2.125rem;
    line-height: 130%;
    color: #FFF;
    margin-left: 2rem;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 3.75rem / 3));
    gap: 1.875rem;
    margin-top: 3.125rem;
  }
  &__cards--active {
    .work-card {
      display: flex !important;
    }
  }
  &__bottom {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 1rem / 3));
    gap: 1rem;
    margin-top: 3.125rem;
  }
  &__btn {
    width: 15rem;
    height: 3.75rem;
    margin: 0 auto;
  }
  &__more {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__more-btn {
    display: flex;
    align-items: center;
    cursor: pointer;
    color: #ffffff;
    &:hover {
      color: #ffc700;
    }
    span {
      font-family: 'RussoOne';
      font-size: 1.25rem;
    }
    i {
      flex-shrink: 0;
      width: 1.25rem;
      display: flex;
      align-items: center;
      margin-left: 1.25rem;
      svg {
        width: 100%;
        height: auto;
      }
    }
    i svg path {
      fill: currentColor;
    }
  }
  &__more-btn--active {
    .works__more-text {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: block;
      }
    }
  }
  &__more-text {
    span:nth-child(2) {
      display: none;
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    top: -18rem;
    right: 0;
    width: 33.125rem;
    pointer-events: none;
  }
  &__img2 {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: -25rem;
    width: 26.875rem;
    pointer-events: none;
  }
}
.work-card {
  display: flex;
  background: linear-gradient(19deg, #000 18.52%, rgba(0, 0, 0, 0.00) 94.54%);
  box-shadow: 0 0.25rem 1.25rem 0 rgba(0, 0, 0, 0.10);
  border-radius: 1.25rem;
  &__img {
    position: relative;
    width: 40%;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 60%;
      height: 60%;
      background: rgba(255, 199, 0, 0.6);
      filter: blur(3.125rem);
    }
    img {
      position: relative;
      z-index: 1;
      margin-top: -1rem;
    }
  }
  &__desc {
    width: 60%;
    padding: 1.875rem 1rem 2.5rem 0.5rem;
  }
  &__title {
    font-family: 'RussoOne';
    font-size: 1.25rem;
    line-height: 130%;
    color: #FFF;
  }
  &__website {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.625rem;
    font-size: 0.875rem;
    line-height: 130%;
    margin-top: 1.25rem;
    span {
      color: #FFF;
    }
    a {
      text-decoration: underline;
      text-underline-offset: 0.1875rem;
      color: #FFC700;
    }
    a:hover {
      color: #ffffff;
      text-decoration: unset;
    }
  }
  &__info {
    font-family: 'Arsenal';
    font-size: 1rem;
    line-height: 130%;
    color: #ffffff;
    margin-top: 0.625rem;
    span {
      font-weight: 700;
    }
  }
}
.work-card:nth-child(6) ~ .work-card {
  display: none;
}
.cooperation {
  background-image: url("../img/cooperation-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #000000;
  padding: 32.5rem 0 20rem;
  margin-top: -23.125rem;
  h2 {
    position: relative;
    z-index: 1;
    max-width: 54.0625rem;
  }
  &__subtitle {
    position: relative;
    z-index: 1;
    display: inline-block;
    background: linear-gradient(53deg, rgba(0, 0, 0, 0.00) 44.57%, rgba(0, 0, 0, 0.50) 60.51%, rgba(0, 0, 0, 0.50) 70.95%), linear-gradient(180deg, rgba(0, 0, 0, 0.00) 52.62%, rgba(0, 0, 0, 0.50) 81.27%, rgba(0, 0, 0, 0.50) 100.03%), rgba(18, 18, 18, 0.50);
