    max-width: 75rem;
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    margin: 5rem auto 0;
    &:after {
      content: '';
      position: absolute;
      top: 70%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 70.625rem;
      height: 70.625rem;
      background-image: url("../img/border3.svg");
      background-repeat: no-repeat;
      background-position: center;
