    }
  }
  &__items-right {
    .cooperation__item {
      margin-left: auto;
    }
    .cooperation__item:nth-child(1) {
      margin-right: 5rem;
    }
  }
  &__item {
    display: flex;
    &-icon {
      flex-shrink: 0;
      width: 1.625rem;
      margin-right: 0.75rem;
    }
    &-text {
      max-width: 22.5rem;
      font-family: 'RussoOne';
      font-size: 1.25rem;
      line-height: 130%;
      color: #FFF;
      span {
        color: #FFC700;
      }
    }
  }
  &__info {
    position: relative;
    z-index: 1;
    max-width: 22.5rem;
    background: linear-gradient(53deg, rgba(0, 0, 0, 0.00) 44.57%, rgba(0, 0, 0, 0.50) 60.51%, rgba(0, 0, 0, 0.50) 70.95%), linear-gradient(180deg, rgba(0, 0, 0, 0.00) 52.62%, rgba(0, 0, 0, 0.50) 81.27%, rgba(0, 0, 0, 0.50) 100.03%), rgba(18, 18, 18, 0.50);
    border: 0.125rem dashed #FFC700;
    backdrop-filter: blur(0.125rem);
    border-radius: 1.25rem;
    padding: 1.25rem 1.875rem;
    margin: 6.25rem 5rem 0 auto;
  }
  &__text {
    font-family: 'RussoOne';
    font-size: 1rem;
    line-height: 130%;
    color: #FFF;
    span {
      color: #FFC700;
    }
  }
  &__btn {
    width: 100%;
    height: 3.75rem;
    margin-top: 1.875rem;
  }
  &__img {
    position: absolute;
    top: -9.375rem;
    left: 47%;
    transform: translate(-50%, 0);
    width: 35rem;
    max-width: unset;
    max-height: unset;
  }
}
.services {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 4.625rem;
    left: 50%;
    transform: translate(-50%, 0);
    width: 105%;
    height: 14rem;
    background-image: url("../img/clip-path.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    display: flex;
    align-items: flex-end;
    gap: 1rem;
    background: linear-gradient(53deg, rgba(0, 0, 0, 0.00) 44.57%, #000 60.51%, #000 70.95%), linear-gradient(180deg, rgba(0, 0, 0, 0.00) 52.62%, #000 81.27%, #000 100.03%), linear-gradient(0deg, #121212 0%, #121212 100%), #FFF;
    border-radius: 3.125rem;
    padding: 8.125rem 5rem 6.25rem;
    margin-top: -11.25rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 3.125rem);
      height: calc(100% - 3.125rem);
      border: 0.125rem dashed #FFF;
      border-radius: 3.125rem;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 20%;
      transform: translate(0, -50%);
      width: 28.75rem;
      height: 28.75rem;
      background: #E37F3D;
      filter: blur(15.625rem);
      border-radius: 50%;
    }
  }
  &__wrap {
    position: relative;
    z-index: 2;
  }
  &__subtitle {
    font-family: 'RussoOne';
    font-size: 2.125rem;
    line-height: 130%;
    color: #FFF;
    margin-top: 3.125rem;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    margin-top: 7.1875rem;
  }
  &__item {
    max-width: 30rem;
    background: #121212;
    border: 0.125rem dashed #FFC700;
    border-radius: 1.25rem;
    padding: 3.125rem;
    font-family: 'RussoOne';
    font-size: 1.25rem;
    line-height: 130%;
    color: #FFF;
  }
  &__item:nth-child(2) {
    transform: rotate(3.465deg);
    margin-top: -1.25rem;
    margin-left: 4.375rem;
  }
  &__item:nth-child(3) {
    transform: rotate(-6.025deg);
    margin-left: 2.8125rem;
  }
  &__btn {
    position: relative;
    z-index: 2;
    width: 18.75rem;
    height: 3.75rem;
    margin-left: -2rem;
    margin-bottom: 6.25rem;
  }
  &__img {
    position: absolute;
    z-index: 1;
    right: -10rem;
    bottom: 0;
    width: 60rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 3.125rem;
      transform: translate(0, -50%);
      width: 19.6875rem;
      height: 19.6875rem;
      background: #FFF;
      filter: blur(15.625rem);
      border-radius: 50%;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
}
