  }
  &__img {
    position: absolute;
    top: 0;
    right: 0;
    width: 50rem;
    max-height: unset;
    pointer-events: none;
  }
  &__img2 {
    position: absolute;
    top: 12rem;
    left: 0;
    width: 32rem;
    max-height: unset;
    pointer-events: none;
  }
}
.services2 {
  padding: 6.25rem 0 13rem 0;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 9.375rem;
    transform: translate(-50%, 0);
    width: 31.25rem;
    height: 31.25rem;
    background: #E37F3D;
    filter: blur(15.625rem);
    border-radius: 50%;
  }
  &__inner {
    position: relative;
    z-index: 1;
  }
  h2 {
    text-align: center;
  }
  &__subtitle {
    text-align: center;
    font-family: 'RussoOne';
    font-size: 2.125rem;
    line-height: 130%;
    color: #ffffff;
    margin-top: 1.875rem;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    margin-top: 5rem;
  }
  &__item {
    max-width: 27.5rem;
    flex-basis: 0;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    background: rgba(18, 18, 18, 0.30);
    border: 0.0625rem dashed #FFC700;
    backdrop-filter: blur(0.625rem);
    border-radius: 3.125rem;
