    &__items-left {
      .cooperation__item:nth-child(3) {
        margin-left: 0;
      }
    }
    &__items-right {
      .cooperation__item:nth-child(1) {
        margin-right: 0;
      }
    }
    &__item {
      &-icon {
        width: 1rem;
        margin-right: 0.5rem;
