    &__right {
      width: 100%;
      &:after {
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: calc(100% - 4rem);
        height: 0.0625rem;
      }
    }
    &__img {
      margin: 1.875rem auto 0;
