h2 {
  font-size: 2.5rem;
  line-height: 130%;
  text-transform: uppercase;
  color: #FFF;
  margin: 0;
  span {
    color: #FFC700;
  }
}
.btn {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
}
.btn-yellow {
  position: relative;
  border-radius: 0.3125rem;
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -0.1875rem;
    transform: translate(-50%, 0);
    width: 80%;
    height: 0.4375rem;
    background: #FFC700;
    filter: blur(0.625rem);
  }
  &:hover {
    span:nth-child(1) {
      background: #ffffff;
    }
    .bg-btn {
      background: #e8e8e8;
    }
  }
  span {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'RussoOne';
    font-size: 1.25rem;
    line-height: 130%;
    color: #181F25;
    background: #FFC700;
    border-radius: 0.3125rem;
    transition: all 0.3s;
  }
  .bg-btn {
    position: absolute;
    z-index: 0;
    top: 0.1875rem;
    left: 0;
    width: 100%;
    height: 100%;
    background: #957506;
    border-radius: 0.3125rem;
  }
}
.form {
  width: 25rem;
  background: rgba(18, 18, 18, 0.50);
  border: 0.125rem dashed #FFC700;
  box-shadow: 0 0.25rem 0.25rem 0 rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(0.625rem);
  border-radius: 3.125rem;
  padding: 3.125rem;
  &-head {
    font-family: 'RussoOne';
    font-size: 1.875rem !important;
    line-height: 130% !important;
    color: #ffffff;
    margin: 0 !important;
  }
  &_block {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25rem;
    margin-top: 1.875rem;
  }
  &-group {
    border: none;
    margin: 0;
    label {
      display: block;
      font-size: 0.875rem;
      line-height: 130%;
      color: #ffffff;
      margin-bottom: 0.3125rem;
    }
    input {
      width: 100%;
      height: 3.75rem;
      background: #F2F2F2;
      border-radius: 0.625rem;
      padding: 0 1.25rem;
      font-family: 'RussoOne';
      font-size: 0.875rem;
      line-height: 130%;
      color: rgba(0, 0, 0, 0.70);
    }
  }
  &-btn {
    width: 100%;
    height: 3.75rem;
    margin-top: 0.625rem;
  }
}
.connection-completed {
  position: relative;
  max-width: 27.8125rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1.875rem;
  background: #121212;
  border: 0.125rem dashed #FFC700;
  box-shadow: 0 0.25rem 0.25rem 0 rgba(0, 0, 0, 0.25);
  border-radius: 3.125rem;
  padding: 3.125rem;
  margin-left: auto;
  &__close {
    position: absolute;
    top: -1.5rem;
    right: 0;
    width: 1.5rem;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__title {
    font-family: 'RussoOne';
    font-size: 1.875rem;
    line-height: 130%;
    color: #FFF;
  }
  &__icon {
    width: 3.75rem;
    img {
      width: 100%;
    }
  }
  &__subtitle {
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: #FFC700;
  }
  &__text {
    font-size: 0.875rem;
    line-height: 130%;
    color: #FFF;
  }
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.4s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.4s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.navigation {
  padding: 3.125rem 0;
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    li {
      font-size: 1.5rem;
      line-height: 130%;
      color: #000;
    }
    li a:hover {
      color: #ffc700;
    }
  }
}

@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2rem;
  }
  .form {
    padding: 2rem;
    &-head {
      font-size: 1.5rem !important;
    }
  }
  .connection-completed {
    max-width: 22rem;
    padding: 2rem;
  }
}

@media screen and (max-width: 1180px) {
  .btn-yellow {
    span {
      font-size: 1.125rem;
    }
  }
  .form {
    max-width: 21rem;
    &-head {
      font-size: 1.25rem !important;
    }
    &-group {
      input {
        height: 3.125rem;
      }
    }
    &-btn {
      height: 3.125rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 1.75rem;
  }
}

@media screen and (max-width: 768px) {
  .form {
    max-width: 15.625rem;
    border-radius: 2rem;
    padding: 1.5rem;
  }
}

@media screen and (max-width: 576px) {
  .form {
    margin-left: auto;
  }
}