.mistake {
  background-image: url("../img/404-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #000000;
  padding: 33.75rem 0;
  &:after {
    content: '';
    position: absolute;
    top: 14.6875rem;
    left: 50%;
    transform: translate(-50%, 0);
    width: 31.25rem;
    height: 31.25rem;
    background: #E37F3D;
    filter: blur(15.625rem);
    border-radius: 50%;
  }
  .container-fluid {
    position: relative;
    z-index: 2;
  }
  &__title {
    position: absolute;
    top: 3.125rem;
    left: 50%;
    transform: translate(-50%, 0);
    font-family: 'RussoOne';
    font-size: 31.25rem;
    line-height: 100%;
    text-transform: uppercase;
    color: #ffffff;
  }
  &__img {
    position: absolute;
    z-index: 1;
    left: 50%;
    bottom: 10rem;
    transform: translate(-50%, 0);
    width: 100%;
  }
  &__title2 {
    display: none;
    font-family: 'RussoOne';
    font-size: 18.75rem;
    line-height: 80%;
    color: #ffffff;
  }
  &__btn {
    width: 18.75rem;
    height: 3.75rem;
    margin-top: 3.75rem;
  }
}

@media screen and (max-width: 1399px) {
  .mistake {
    padding: 12rem 0 25rem 0;
    &:after {
      display: none;
    }
    &__title {
      display: none;
    }
    &__title2 {
      display: block;
    }
    h2 {
      margin-top: 1rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .mistake {
    padding: 16rem 0 20rem;
    &__title2 {
      font-size: 12.5rem;
    }
    &__btn {
      width: 15rem;
      height: 3.125rem;
      margin-top: 2rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .mistake {
    padding-top: 13rem;
    &__title2 {
      font-size: 9.375rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .mistake {
    padding: 15rem 0 10rem;
  }
}

@media screen and (max-width: 576px) {
  .mistake {
    padding: 10rem 0;
  }
}